import uuid from 'uuid/v4';
import { ensureArray, decodeDate, getTime } from '../utils';


/**
 * 
 * @enum {Symbol}
 * @since 3.2.0
 * @memberof module:leancloud-realtime
 */
const MessageStatus = {
  /**  */
  NONE: Symbol('none'),
  /**  */
  SENDING: Symbol('sending'),
  /**  */
  SENT: Symbol('sent'),
  /**  */
  DELIVERED: Symbol('delivered'),
  /**  */
  FAILED: Symbol('failed'),
};
Object.freeze(MessageStatus);

const rMessageStatus = {
  [MessageStatus.NONE]: true,
  [MessageStatus.SENDING]: true,
  [MessageStatus.SENT]: true,
  [MessageStatus.DELIVERED]: true,
  [MessageStatus.READ]: true,
  [MessageStatus.FAILED]: true,
};

export { MessageStatus };
export default class Message {
  /**
   * @implements AVMessage
   * @param  {Object|String|ArrayBuffer} content 
   */
  constructor(content) {
    Object.assign(this, { content }, {
      /**
       * @type {String}
       * @memberof Message#
       */
      id: uuid(),
      /**
       *  conversation id
       * @memberof Message#
       * @type {String?}
       */
      cid: null,
      /**
       * 
       * @memberof Message#
       * @type {Date}
       */
      timestamp: new Date(),
      /**
       * 
       * @memberof Message#
       * @type {String}
       */
      from: undefined,
      /**
       * 
       * @since 4.0.0
       * @memberof Message#
       * @type {String[]}
       */
      mentionList: [],
      /**
       * 
       * @since 4.0.0
       * @memberof Message#
       * @type {Boolean}
       */
      mentionedAll: false,
      _mentioned: false,
    });
    this._setStatus(MessageStatus.NONE);
  }

  /**
   *  JSON 
   * @private
   * @return {Object}
   */
  getPayload() {
    return this.content;
  }

  _toJSON() {
    const {
      id,
      cid,
      from,
      timestamp,
      deliveredAt,
      updatedAt,
      mentionList,
      mentionedAll,
      mentioned,
    } = this;
    return {
      id,
      cid,
      from,
      timestamp,
      deliveredAt,
      updatedAt,
      mentionList,
      mentionedAll,
      mentioned,
    };
  }

  /**
   *  JSON 
   * @return {Object}  plain Object
   */
  toJSON() {
    return {
      ...this._toJSON(),
      data: this.content,
    };
  }

  /**
   *  JSON  toJSON  {@link IMClient#parseMessage} 
   * @return {Object}  plain Object
   * @since 4.0.0
   */
  toFullJSON() {
    const {
      content,
      id,
      cid,
      from,
      timestamp,
      deliveredAt,
      _updatedAt,
      mentionList,
      mentionedAll,
    } = this;
    return ({
      data: content,
      id,
      cid,
      from,
      timestamp: getTime(timestamp),
      deliveredAt: getTime(deliveredAt),
      updatedAt: getTime(_updatedAt),
      mentionList,
      mentionedAll,
    });
  }

  /**
   *  {@link module:leancloud-realtime.MessageStatus} 
   * @type {Symbol}
   * @readonly
   * @since 3.2.0
   */
  get status() {
    return this._status;
  }

  _setStatus(status) {
    if (!rMessageStatus[status]) {
      throw new Error('Invalid message status');
    }
    this._status = status;
  }

  get timestamp() {
    return this._timestamp;
  }
  set timestamp(value) {
    this._timestamp = decodeDate(value);
  }

  /**
   * 
   * @type {?Date}
   */
  get deliveredAt() {
    return this._deliveredAt;
  }
  set deliveredAt(value) {
    this._deliveredAt = decodeDate(value);
  }

  /**
   *  timestamp 
   * @type {Date}
   * @since 3.5.0
   */
  get updatedAt() {
    return this._updatedAt || this.timestamp;
  }
  set updatedAt(value) {
    this._updatedAt = decodeDate(value);
  }

  /**
   * 
   * @type {Boolean}
   * @readonly
   * @since 4.0.0
   */
  get mentioned() {
    return this._mentioned;
  }
  _updateMentioned(client) {
    this._mentioned =
      this.from !== client &&
      (this.mentionedAll || this.mentionList.indexOf(client) > -1);
  }
  /**
   * 
   * @since 4.0.0
   * @return {String[]}  id 
   */
  getMentionList() {
    return this.mentionList;
  }
  /**
   * 
   * @since 4.0.0
   * @param {String[]} clients  id 
   * @return {this} self
   */
  setMentionList(clients) {
    this.mentionList = ensureArray(clients);
    return this;
  }
  /**
   * 
   * @since 4.0.0
   * @param {Boolean} [value=true]
   * @return {this} self
   */
  mentionAll(value = true) {
    this.mentionedAll = Boolean(value);
    return this;
  }

  /**
   *  Message
   *  true
   * @private
   * @returns {Boolean}
   * @implements AVMessage.validate
   */
  static validate() {
    return true;
  }

  /**
   * 
   * <pre>
   *  message message
   *  json  content  Message
   * @private
   * @param  {Object}  json    json 
   * @param  {Message} message  message
   * @return {Message}
   * @implements AVMessage.parse
   */
  static parse(json, message) {
    return message || new this(json);
  }
}
